package adventOfCode.util.ui

import colibri.Subject
import outwatch._
import outwatch.dsl._

object Helpers {
  def toggleSwitch(isChecked: Subject[Boolean], checkedLabel: String, uncheckedLabel: String): VNode = {
    val uncheckedOpacity = 0.3
    label(
      cls := "relative flex justify-between items-center text-xl",
      span(
        uncheckedLabel,
        isChecked.map(VModifier.ifTrue(_)(opacity := uncheckedOpacity)),
        cls := "cursor-pointer",
      ),
      input(
        tpe := "checkbox",
        //checked <-- [Observable] isChecked, //type annotation to make IDEA happy
        onChange.checked --> isChecked,
        cls := "absolute left-1/2 -translate-x-1/2 w-full h-full peer appearance-none rounded-md cursor-pointer",
      ),
      span(
        cls := "w-20 h-10 flex items-center flex-shrink-0 mx-4 p-1 bg-gray-300 rounded-full duration-300 ease-in-out after:w-8 after:h-8 after:bg-white after:rounded-full after:shadow-md after:duration-300 peer-checked:after:translate-x-10 cursor-pointer",
      ),
      span(
        checkedLabel,
        isChecked.map(VModifier.ifNot(_)(opacity := uncheckedOpacity)),
        cls := "cursor-pointer",
      ),
    )
  }

  object FormattedTable {

    val td: VNode    = dsl.td(cls := "border border-gray-300 align-baseline")
    val th: VNode    = dsl.th(cls := "border border-gray-300 align-baseline")
    val table: VNode = dsl.table(cls := "border-collapse border border-gray-400")

  }
}
